import React, { useMemo } from 'react'
import styled from 'styled-components'
import { Pair } from '@pancakeswap2/sdk/dist/dist'
import { Text, Flex, CardBody, CardFooter, Button, AddIcon , ButtonMenu, ButtonMenuItem} from '@pancakeswap/uikit'
import { Link, useLocation } from 'react-router-dom'
import { useTranslation } from 'contexts/Localization'
import useActiveWeb3React from 'hooks/useActiveWeb3React'
import FullPositionCard from '../../components/PositionCard4'
import { useTokenBalancesWithLoadingIndicator } from '../../state/wallet/hooks4'
import { usePairs } from '../../hooks/usePairs4'
import { toV2LiquidityToken, useTrackedTokenPairs } from '../../state/user/hooks/index4'
import Dots from '../../components/Loader/Dots'
import { AppHeader, AppBody } from '../../components/App/index4'
import Page from '../Page4'

const Body = styled(CardBody)`
  background-color: ${({ theme }) => theme.colors.dropdownDeep};
`
const StyledNav = styled.nav`
  margin-top: 40px;
`

export default function Pool4() {
  const { account } = useActiveWeb3React()
  const { t } = useTranslation()
  const location = useLocation()

  // fetch the user's balances of all tracked V2 LP tokens
  const trackedTokenPairs = useTrackedTokenPairs()
  const tokenPairsWithLiquidityTokens = useMemo(
    () => trackedTokenPairs.map((tokens) => ({ liquidityToken: toV2LiquidityToken(tokens), tokens })),
    [trackedTokenPairs],
  )
  const liquidityTokens = useMemo(
    () => tokenPairsWithLiquidityTokens.map((tpwlt) => tpwlt.liquidityToken),
    [tokenPairsWithLiquidityTokens],
  )
  const [v2PairsBalances, fetchingV2PairBalances] = useTokenBalancesWithLoadingIndicator(
    account ?? undefined,
    liquidityTokens,
  )
  const getActiveIndex = (pathname: string): number => {
    if (
      pathname.includes('/pool4') ||
      pathname.includes('/createradio') ||
      pathname.includes('/addradioliquidity') ||
      pathname.includes('/removeradio') ||
      pathname.includes('/radiofind') ||
      pathname.includes('/radioliquidity')
    ) {
      return 2
    }
    return 0
  }

  // fetch the reserves for all V2 pools in which the user has a balance
  const liquidityTokensWithBalances = useMemo(
    () =>
      tokenPairsWithLiquidityTokens.filter(({ liquidityToken }) =>
        v2PairsBalances[liquidityToken.address]?.greaterThan('0'),
      ),
    [tokenPairsWithLiquidityTokens, v2PairsBalances],
  )

  const v2Pairs = usePairs(liquidityTokensWithBalances.map(({ tokens }) => tokens))
  const v2IsLoading =
    fetchingV2PairBalances || v2Pairs?.length < liquidityTokensWithBalances.length || v2Pairs?.some((V2Pair) => !V2Pair)

  const allV2PairsWithLiquidity = v2Pairs.map(([, pair]) => pair).filter((v2Pair): v2Pair is Pair => Boolean(v2Pair))

  const renderBody = () => {
    if (!account) {
      return (
        <Text color="textSubtle" textAlign="center">
          {t('Connect to a wallet to view your liquidity.')}
        </Text>
      )
    }
    if (v2IsLoading) {
      return (
        <Text color="textSubtle" textAlign="center">
          <Dots>{t('Loading')}</Dots>
        </Text>
      )
    }
    if (allV2PairsWithLiquidity?.length > 0) {
      return allV2PairsWithLiquidity.map((v2Pair, index) => (
        <FullPositionCard
          key={v2Pair.liquidityToken.address}
          pair={v2Pair}
          mb={index < allV2PairsWithLiquidity.length - 1 ? '16px' : 0}
        />
      ))
    }
    return (
      <Text color="textSubtle" textAlign="center">
        {t('No liquidity found.')}
      </Text>
    )
  }

  return (
    <Page>
      <AppBody>
        <AppHeader title={t('Your Liquidity')} subtitle={t('Remove liquidity to receive tokens back')} />
        <Body>
          {renderBody()}
          {account && !v2IsLoading && (
            <Flex flexDirection="column" alignItems="center" mt="24px">
              <Text color="textSubtle" mb="8px">
                {t("Don't see a pool you joined?")}
              </Text>
              <Button id="import-pool-link" variant="secondary" scale="sm" as={Link} to="/radiofind">
                {t('Find other LP tokens')}
              </Button>
            </Flex>
          )}
        </Body>
        <CardFooter style={{ textAlign: 'center' }}>
          <Button id="join-pool-button" as={Link} to="/addradioliquidity" width="100%" startIcon={<AddIcon color="white" />}>
            {t('Add Liquidity')}
          </Button>
        </CardFooter>
      </AppBody>
      <StyledNav>
      <ButtonMenu activeIndex={getActiveIndex(location.pathname)} scale="sm" variant="subtle">
        <ButtonMenuItem id="swap-nav-link" to="/liquidity" as={Link}>
          {t('PancakeSwap')}
        </ButtonMenuItem>
        <ButtonMenuItem id="pool-nav-link" to="/apeliquidity" as={Link}>
          {t('ApeSwap')}
        </ButtonMenuItem>
        <ButtonMenuItem id="swap-nav-link" to="/radioliquidity" as={Link}>
          {t('RadioShack')}
        </ButtonMenuItem>
      </ButtonMenu>
      </StyledNav>
    </Page>
    
  )
}
